import React from 'react';

interface IHSLColor {
  a?: number;
  h: number;
  l: number;
  s: number;
}

interface IRGBColor {
  a?: number;
  b: number;
  g: number;
  r: number;
}

type Color = string | IHSLColor | IRGBColor;

interface IColorResult {
  hex: string;
  hsl: IHSLColor;
  rgb: IRGBColor;
}

type ColorChangeHandler = (color: IColorResult) => void;

interface ISketchPickerProps {
  disableAlpha?: boolean;
  presetColors?: string[];
  width?: string;
  color?: Color;
  hsl?: { h: number; s: number; l: number; a?: number };
  rgb?: { r: number; g: number; b: number; a?: number };
  hsv?: { h: number; s: number; v: number };
  hex?: string;
  styles?: React.CSSProperties;
  passedStyles?: any;
  className?: string;
  onSwatchHover?: (color: IColorResult, event: React.MouseEvent) => void;
  onChange?: ColorChangeHandler;
  onChangeComplete?: ColorChangeHandler;
}


export default class SketchPicker extends React.Component<ISketchPickerProps> {}
